import React, { useState, useEffect } from 'react';
import { collection, getDocs } from 'firebase/firestore';
import { db as firebaseDb } from './firedb';

import './resultAll.css';

const ResultAll = () => {
  const [allPlayerData, setAllPlayerData] = useState([]);
  const [visiblePlayers, setVisiblePlayers] = useState(40); // Number of initially visible players
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchAllPlayerData = async () => {
      try {
        const playersCollectionRef = collection(firebaseDb, 'players');
        const querySnapshot = await getDocs(playersCollectionRef);

        // Map documents to an array
        const allPlayerDataArray = querySnapshot.docs
          .map((doc) => ({ id: doc.id, ...doc.data() }));

        // Calculate new total wins for each player
        const updatedPlayerData = allPlayerDataArray.map(player => ({
          ...player,
          newTotalWins: calculateNewTotalWins(player, allPlayerDataArray)
        }));

        // Sort by new total wins and date
        const sortedPlayerData = updatedPlayerData.sort((a, b) => {
          const winsComparison = b.newTotalWins - a.newTotalWins;
          if (winsComparison !== 0) {
            return winsComparison;
          }
          return new Date(b.date) - new Date(a.date);
        });

        // Display each player only once
        const displayedPlayers = new Set();
        const uniquePlayerData = sortedPlayerData.filter(player => {
          if (!displayedPlayers.has(player.fullName)) {
            displayedPlayers.add(player.fullName);
            return true;
          }
          return false;
        });

        setAllPlayerData(uniquePlayerData);
        setLoading(false); // Set loading to false after data is fetched
      } catch (error) {
        console.error('Error fetching all player data from Firebase:', error.message);
      }
    };

    const calculateNewTotalWins = (player, allData) => {
      return allData
        .filter((p) => p.fullName === player.fullName)
        .reduce((sum, p) => sum + p.wins, 0);
    };

    fetchAllPlayerData();
  }, []);

  const handleShowMore = () => {
    setVisiblePlayers(prev => prev + 10); // Increase the number of visible players by 10
  };

  if (loading) {
    return <p>Loading...</p>; // You can customize the loading indicator
  }

  if (allPlayerData.length === 0) {
    return <p>No player data available.</p>; // You can customize this message
  }

  return (
    <div className="container">
      <h2 className="heading">Maroubra Seals Snooker Result</h2>
      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>Last Played</th>
            <th>Player Name</th>
            <th>Handicap</th>
            <th>Last Wins</th>
            <th>Total Wins</th>
          </tr>
        </thead>
        <tbody>
          {allPlayerData.slice(0, visiblePlayers).map((player, index) => (
            <tr key={player.id} className={index < 3 ? 'top-rank' : ''}>
              <td>{index + 1}</td>
              <td>{player.date}</td>
              <td className="name-cell">
                {player.fullName}
              </td>
              <td>{player.handicap}</td>
              <td>{player.wins}</td>
              <td>{player.newTotalWins}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {visiblePlayers < allPlayerData.length && (
        <button onClick={handleShowMore}>Show More</button>
      )}
    </div>
  );
};

export default ResultAll;
