import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar';
import { collection, getDocs, query, where, orderBy, limit } from 'firebase/firestore';
import { db as firebaseDb } from './firedb';
import { startOfWeek, endOfWeek } from 'date-fns';

import './resultFire.css';

const ResultFire = () => {
  const [dateList, setDateList] = useState([]);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [playerData, setPlayerData] = useState([]);
  const [latestResult, setLatestResult] = useState(null);
  const [showWeeklyResult, setShowWeeklyResult] = useState(false);

  useEffect(() => {
    fetchDateList();
    fetchLatestResult();

   // Automatically select the closest Monday of the current date within the same week
   const currentDate = new Date();
   const dayOfWeek = currentDate.getDay(); // 0 (Sunday) to 6 (Saturday)
   const daysUntilMonday = (dayOfWeek - 1 + 7) % 7; // Calculate days until the next Monday
   const closestMonday = new Date(currentDate);
   closestMonday.setDate(currentDate.getDate() - daysUntilMonday);

   // Set the closest Monday as the selected date
   setSelectedDate(closestMonday);
   fetchPlayerDataByDate(closestMonday); // Fetch player data for the closest Monday

 }, []); // Empty dependency array ensures this effect runs only once on mount

  const fetchDateList = async () => {
    try {
      const playersCollectionRef = collection(firebaseDb, 'players');
      const querySnapshot = await getDocs(playersCollectionRef);
      const uniqueDates = new Set();
      querySnapshot.forEach((doc) => {
        const date = new Date(doc.data().date);
        if (date) {
          uniqueDates.add(date);
        }
      });
      const sortedDates = Array.from(uniqueDates).sort((a, b) => b - a);
      setDateList(sortedDates);
    } catch (error) {
      console.error('Error fetching date list from Firebase:', error.message);
    }
  };

  const fetchPlayerDataByDate = async (date) => {
    try {
      const playersCollectionRef = collection(firebaseDb, 'players');
      const formattedDate = new Date(date.getTime() - (date.getTimezoneOffset() * 60000)).toISOString().split('T')[0];
      const dateQuery = query(
        playersCollectionRef,
        where('date', '==', formattedDate)
      );
      const querySnapshot = await getDocs(dateQuery);
      const playerDataForDate = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
  
      // Sort player data by wins in descending order
      const sortedPlayerData = playerDataForDate.sort((a, b) => b.wins - a.wins);
  
      setPlayerData(sortedPlayerData);
    } catch (error) {
      console.error('Error fetching player data from Firebase:', error.message);
    }
  };
  
  
  

  const fetchLatestResult = async () => {
    try {
      const playersCollectionRef = collection(firebaseDb, 'players');
      const latestResultQuery = query(
        playersCollectionRef,
        orderBy('date', 'desc'),
        limit(5)
      );
      const querySnapshot = await getDocs(latestResultQuery);
      const latestResultData = querySnapshot.docs[0]?.data();
      setLatestResult(latestResultData);
    } catch (error) {
      console.error('Error fetching latest result from Firebase:', error.message);
    }
  };

  const handleDateChange = async (date) => {
    setSelectedDate(date);
    await fetchPlayerDataByDate(date);
  };
  

  const handleToggleWeeklyResult = () => {
    setShowWeeklyResult(!showWeeklyResult);
  };

  return (
    <div className="container">
      <h2 className="heading">Maroubra Seals Weekly Result</h2>
      <button className="toggle-button" onClick={handleToggleWeeklyResult}>
        {showWeeklyResult ? 'Hide Calander' : 'Check Another Date Result'}
      </button>
      {showWeeklyResult && (
        <div className="calendar-section">
          <div className="calendar">
            <Calendar
              onChange={handleDateChange}
              value={selectedDate}
              tileClassName={({ date, view }) =>
                view === 'month' && date.getMonth() !== selectedDate.getMonth()
                  ? 'other-month'
                  : null
              }
            />
          </div>
        
        </div>
      )}
        <div className="date-info">
            <h3>Selected Date: {selectedDate.toLocaleDateString()}</h3>
            {playerData.length > 0 ? (
              <div>
                <table>
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Player Name</th>
                      <th>Handicap</th>
                      <th>Last Wins</th>
                    </tr>
                  </thead>
                  <tbody>
                    {playerData.map((player,index) => (
                      <tr key={player.id}>
                        <td className="wider-cell-rank">{index+1}</td>
                        <td className="wider-cell">{player.fullName}</td>
                        <td className="wider-cell">{player.handicap}</td>
                        <td className="wider-cell">{player.wins}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            ) : (
              <p>No tournament on this day. Select another day.</p>
            )}
          </div>
    </div>
  );
};

export default ResultFire;
