import './App.css';
import ResultAll from './resultAll';
import ResultFire from './resultFire';
import Handicap from './handicap';
import { useState } from 'react';

function App() {
  const [activePage, setActivePage] = useState('ResultAll');



  const handlePageChange = (page) => {
    setActivePage(page);
  };

  return (
    <div className="App">
      <nav>
        <ul>
          <li className={activePage === 'ResultAll' ? 'active' : ''} onClick={() => handlePageChange('ResultAll')}>
            All Results
          </li>
          <li className={activePage === 'ResultFire' ? 'active' : ''} onClick={() => handlePageChange('ResultFire')}>
            Weekly
          </li>
          <li className={activePage === 'Handicap' ? 'active' : ''} onClick={() => handlePageChange('Handicap')}>
            Handicap Details
          </li>
        </ul>
      </nav>
      {activePage === 'ResultAll' && <ResultAll />}
      {activePage === 'ResultFire' && <ResultFire />}
      {activePage === 'Handicap' && <Handicap />}
      
    </div>
  );
}

export default App;
