import React, { useState, useEffect } from 'react';
import { collection, getDocs } from 'firebase/firestore';
import { db as firebaseDb } from './firedb';

import './resultFire.css';

const Handicap = () => {
  const [playerData, setPlayerData] = useState([]);
  const [expandedPlayer, setExpandedPlayer] = useState(null);

  useEffect(() => {
    fetchPlayerData();
  }, []);

  const fetchPlayerData = async () => {
    try {
      const playersCollectionRef = collection(firebaseDb, 'players');
      const querySnapshot = await getDocs(playersCollectionRef);
      const playerDataList = [];
      const playerNamesSet = new Set();

      querySnapshot.forEach((doc) => {
        const data = doc.data();
        const fullName = data.fullName;
        const handicap = data.handicap;
        const wins = data.wins;
        const date = new Date(data.date).toISOString().split('T')[0];

        if (!playerNamesSet.has(fullName)) {
          playerNamesSet.add(fullName);
          playerDataList.push({ fullName, handicaps: [{ date, handicap, wins }] });
        } else {
          const existingPlayerIndex = playerDataList.findIndex(player => player.fullName === fullName);
          playerDataList[existingPlayerIndex].handicaps.push({ date, handicap, wins });
        }
      });

  // Sort handicaps array in descending order by date for each player
playerDataList.forEach(player => {
    player.handicaps.sort((a, b) => new Date(b.date) - new Date(a.date));
    
  
  });
  

      setPlayerData(playerDataList);
    } catch (error) {
      console.error('Error fetching player data from Firebase:', error.message);
    }
  };

  const toggleExpand = (index) => {
    if (index === expandedPlayer) {
      setExpandedPlayer(null);
    } else {
      setExpandedPlayer(index);
    }
  };

  // Sort playerData array by current handicap in ascending order
const sortedPlayerData = [...playerData].sort((a, b) => {
    // Get the handicap values for comparison
    const handicapA = a.handicaps.length > 0 ? a.handicaps[0].handicap : Infinity;
    const handicapB = b.handicaps.length > 0 ? b.handicaps[0].handicap : Infinity;
    
    // Compare handicaps
    return handicapA - handicapB;
  });

  return (
    <div className="container">
      <h2 className="heading">Maroubra Seals Handicap Adjustment</h2>
      <div className="date-info">
        <table>
          <thead>
            <tr>
              <th>#</th>
              <th>Player Name</th>
              <th>Current Handicap</th>
            </tr>
          </thead>
          <tbody>
{sortedPlayerData.map((player, index) => (
  <React.Fragment key={index}>
    <tr onClick={() => toggleExpand(index)}>
      <td>{index + 1}</td>
      <td>{player.fullName}</td>
      <td>{player.handicaps.length > 0 ? player.handicaps[0].handicap : '-'}</td>
    </tr>
    {index === expandedPlayer && (
      <tr>
        <td colSpan="3">
          <table>
            <thead>
              <tr>
                <th>Date</th>
                <th>Wins</th>
                <th>AdjustedHandicap</th>
              </tr>
            </thead>
            <tbody>
              {player.handicaps.map((handicap, i) => (
                <tr key={i}>
                  <td>{handicap.date}</td>
                  <td>{handicap.wins}</td>
                  <td>{handicap.handicap}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </td>
      </tr>
    )}
  </React.Fragment>
))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Handicap;
